.heading {
  height: 2rem;
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-around;
  font-size: 25px;
  font-family: fantasy;
  letter-spacing: 5px;
  margin-top: 5px;
}

.box {
  display: flex;
  width: 70%;
  background-color: #005986;
  flex-direction: column;
  margin: auto;
  color: white;
  align-items: center;
  border-radius: 10px;
  height: 400px;
}
.inputData {
  margin-top: 20px;
}
.inputField {
  font-size: 16px;
  line-height: 18.4px;
  text-decoration: none solid rgb(255, 255, 255);
  text-align: center;
  word-spacing: 0px;
  background-color: #296480;
  height: 38px;
  width: 380px;
  padding: 5px 38px 5px 6px;
  display: inline-block;
  cursor: text;
  border-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.search {
  margin: -36px;
}
.inputField::placeholder {
  color: rgb(5, 31, 49);
  font-size: large;
  font-family: cursive;
  font-weight: bolder;
}
.failure {
  font-size: x-large;
  font-family: cursive;
  font-weight: bolder;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: cursive;
}

.location {
  color: wheat;
  padding: 12px;
}

/* media queries */

@media screen and (max-width: 1080px) {
  .inputField {
    width: 250px;
  }
  .box {
    margin-top: 30px;
  }
}
@media screen and (max-width: 850px) {
  .inputField {
    width: 250px;
  }
}
@media screen and (max-width: 760px) {
  .inputData {
    margin-right: 42px;
  }
  .inputField {
    width: 220px;
    padding: 5px 38px 5px 6px;
  }
  .heading {
    font-size: 21px;
  }
}
@media screen and (max-width: 580px) {
  .inputField {
    width: 250px;
  }
  .heading {
    font-size: 18px;
  }
}
@media screen and (max-width: 520px) {
  .heading {
    font-size: 17px;
  }
}

@media screen and (max-width: 394px) {
  .inputField {
    width: 222px;
    margin-right: 3px;
  }
  .location {
    margin-bottom: 0;
  }

  .humidity .tempmin_max .temp .humidity {
    margin-top: 0;
    margin-bottom: 0;
  }
}
@media screen and (max-width: 280px) {
  .inputField {
    width: 154px;
    height: 28px;
    margin-right: 10px;
  }
  .failure {
    font-size: small;
  }
  .info {
    font-size: 14px;
    padding: 0px;
  }
  .tempmin_max {
    margin-left: 8px;
  }
}
